<template>
  <div class="file-item">
    <!-- 文件icon -->
    <i
      :class="[`icon-filetype-${data.extension}`, this.mode === 'normal' ? '' : 'error']"
      class="iconfont" />

    <!-- 文件内容 -->
    <div class="file-info">
      <p class="file-name text-one-line">{{ data.fullName }}</p>

      <!-- 上传进度条 -->
      <div v-if="data.percentage < 100 && data.extension !== 'json'" class="file-progress-box">
        <el-progress :percentage="data.percentage" :show-text="false" />
        <i class="iconfont icon-circle-delete" @click="removeEffect" />
      </div>

      <!-- 上传状态提示语 -->
      <div class="upload-status-message">
        <p v-if="mode === 'normal'" class="flex">
          <!-- 上传过程中显示的文字 -->
          <span v-if="data.percentage < 100 && data.extension !== 'json'" class="file-size">
            {{ data.size * data.percentage * 0.01 | byte }}
            of {{ data.size | byte }} ({{ data.percentage }}%）
          </span>
          <!-- 上传结束时显示的文字 -->
          <span v-else>
            <span class="file-status"> Loaded</span>
            <span class="file-size">{{ data.size | byte }}</span>
          </span>
        </p>
        <!-- 缺失ctm文件时，显示错误状态的文字 -->
        <p v-else class="upload-error-message">
          <!-- Failed to load -->
          <span class="file-status"> Loaded</span>
          <span class="file-size">{{ data.size | byte }}</span>
        </p>
      </div>
    </div>

    <!-- 删除和阅览按钮 -->
    <div v-if="!data.percentage < 100" class="modefy-file-status">
      <i class="iconfont icon-file-delete" @click="removeEffect" />
      <i
        :class="visible ? 'icon-eye-slash' : 'icon-eye'"
        class="iconfont"
        @click="toggleVisible" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    // 文件对象（已对数据结构处理过）
    data: {
      type: Object,
      required: true,
    },
    // 显示模式： normal / error
    mode: {
      type: String,
      default: 'normal',
    },
  },
  inject: ['vm'],
  data() {
    return {
      visible: true,
    };
  },
  methods: {
    // 切换显示与隐藏部件在预览图上的状态
    toggleVisible() {
      this.visible = !this.visible;
      this.vm.$emit('toggle-visible', {
        fullName: this.data.fullName,
        extension: this.data.extension,
        visible: this.visible,
      });
    },
    // 移除文件在预览图中的效果
    removeEffect() {
      this.vm.$emit('delete-file', this.data);
    },
  },
};
</script>

