<template>
  <div class="upload-model-container" ref="uploadModel">
    <!-- 上传模型信息 -->
    <upload-model-info class="upload-section" ref="uploadInfo" />

    <!-- 上传模型文件 -->
    <upload-model-files class="upload-section" ref="uploadfiles" />

    <!-- 上传按钮 -->
    <section class="upload-section submit">
      <el-button @click="$router.push({ name: 'FileList' })">
        {{ $t('public.dialog.cancel') }}</el-button>
      <el-button type="primary" @click="submit">{{ $t('public.dialog.save') }}</el-button>
    </section>
  </div>
</template>

<script>
import Vue from 'vue';


import mixinLoading from '@/mixins/loading';
import { uploadModelData } from '@/api/shoe';
import { blockDragEffect } from '@/utils/public';
import { blob2Text } from '_/blob-utils';
import UploadModelFiles from './components/UploadModelFiles';
import UploadModelInfo from './components/UploadModelInfo';

export default {
  components: {
    UploadModelInfo,
    UploadModelFiles,
  },
  // 通过路由传入参数
  props: {
    parentFolderId: {
      type: [String, Number],
      default: '',
    },
    workspaceId: {
      type: [String, Number],
      default: '',
    },
    appType: {
      type: [String, Number],
      default() {
        return this.$route.query.appType ? +this.$route.query.appType : null;
      },
    },
  },
  mixins: [mixinLoading],
  provide() {
    return {
      vm: this.vm,
    };
  },
  data() {
    return {
      vm: new Vue(), // 示例化一个vue，做该部件的事件传递的中间者
    };
  },
  mounted() {
    // 禁止浏览器拖拽的默认行为
    blockDragEffect(this.$refs.uploadModel);
  },
  destroyed() {
    this.vm = null;
  },
  methods: {
    // 提交表单
    async submit() {
      this.startLoading();
      try {
        // 校验表单
        const formInfo = await this.$refs.uploadInfo.validateForm();
        // 校验文件
        let fileInfo = await this.$refs.uploadfiles.validateFiles();

        // 对表单数据结构做处理
        await this._formatFormInfo(formInfo);
        // 对文件数据结构做处理
        fileInfo = await this._formatFileInfo(fileInfo);

        // 接口参数
        const params = {
          appType: this.appType || (this.workspaceId ? 13 : 11),
          parentFolderId: this.parentFolderId || '-1',
          workspaceId: this.workspaceId,
        };

        await uploadModelData({ components: fileInfo, ...formInfo, ...params }, this.workspaceId);

        // 如果是运营平台跳过来的
        if (/^\/fragment/.test(this.$route.path)) {
          window.close();
          return;
        }

        // 上传后根据不同传入值，跳转到对应的列表空间
        let query = {};
        // eslint-disable-next-line
        this.workspaceId ? (query = { appType: 13, relateId: this.workspaceId, id: this.parentFolderId, })
          : (query = { appType: 11, id: +this.parentFolderId !== -1 ? this.parentFolderId : '' });

        this.$router.push({ name: 'FileList', query });
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.endLoading();
      }
    },
    // 根据上传接口，对材质信息对象做数据结构调整
    async _formatFormInfo(form) {
      // 对时间格式改为时间戳上传
      if (form.dueDate) {
        form.dueDate = form.dueDate.getTime();
      }
      // 如果没有上传预览图，在模型阅览图中截取
      if (!form.curtailImg) {
        form.curtailImg = await this.$refs.uploadfiles.getModelSnapshot();
      }
    },
    // 根据上传接口，对材质文件对象做数据结构调整
    async _formatFileInfo(files) {
      const result = [];
      await Promise.all(files.map(async (file) => {
        // 忽略掉没有ctm文件的数据
        if (file.ctm) {
          const { name, path, size } = file.ctm;
          // 如果有对应的json文件，将JSON数据转化为字符串形式存到 edgeArr 字段中
          let edgeArr = '';
          if (file.json) edgeArr = await blob2Text(file.json.fileObj);

          // 根据接口文档，创建对应的的上传数据结构对象
          const newObj = {
            componentName: name,
            componentPath: path,
            fileSize: size,
            edgeArr,
          };

          result.push(newObj);
        }
      }));
      return result;
    },
  },
};
</script>

<style lang="scss" scoped>
.upload-model-container {
  width: 1200px;
  margin: 0 auto 20px;

  .upload-section {
    margin-top: 20px;
    background: #fff;
    box-shadow: 0px 0px 15px 0px rgba(136, 149, 172, 0.08);
    border-radius: 4px;
    &.submit {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      height: 80px;
      padding-right: 40px;
    }
  }
}
</style>
