<template>
  <div>
    <!-- 鞋模型效果预览 -->
    <div class="previewer-box">
      <canvas width="394" height="330" ref="componentPreviewer" v-on="canvasEvent">
      </canvas>
      <div class="proviewer-box-tip">
        <!-- eslint-disable-next-line -->
        <el-popover width="280" trigger="hover" :content="$t('upload.model.suggest_reupload')">
          <i slot="reference" class="iconfont icon-help"></i>
        </el-popover>
        <p class="tip-text">
          <span>{{ $t('upload.model.inverse_surface_color') }}</span>
          <i class="tip-color-shape"></i>
        </p>
      </div>
      <p class="previewer-title">{{ $t('upload.model.model_preview') }}</p>
    </div>

    <!-- JSON文件预览 -->
    <div class="previewer-box" ref="jsonPreviewer">
      <p class="previewer-title">{{ $t('upload.model.json_preview') }}</p>
    </div>
  </div>
</template>

<script>
import mixinRenderer from '@/views/model/mixin-renderer';

import { loadD4, loadD2 } from '@/utils/load-d4';
import { dataUrl2Blob, blob2u8a, blob2Text } from '_/blob-utils';
import uploadBlobToOSS from '@/utils/oss/uploadBlobToOSS';


export default {
  inject: ['vm'],
  mixins: [mixinRenderer],
  mounted() {
    this.initRenderer();
    // 处理渲染文件事件
    this.vm.$on('render-file', this.handleRenderFile);
    // 处理更改渲染状态事件
    this.vm.$on('toggle-visible', this.togglePreviewerEffect);
    // 处理删除文件
    this.vm.$on('delete-file', this.removePreviewerEffect);
  },
  methods: {
    // 初始化渲染效果图
    async initRenderer() {
      // eslint-disable-next-line
      const D4 = await loadD4();
      const D2 = await loadD2();

      this.d4 = this.instanceD4(D4, this.$refs.componentPreviewer);
      this.canvasEvent = this._initCanvasEvent();

      this.d2 = new D2();
      this.d2.create2DViewer(this.$refs.jsonPreviewer);
    },
    // 渲染文件，显示到效果图上；
    async handleRenderFile(file) {
      const extension = file.name.split('.').pop()?.toLowerCase();
      // 根据不同的文件，调用不同的渲染方法
      if (extension === 'ctm') {
        const stream = await blob2u8a(file);
        this.d4.loadComponent(file.name, stream);
      } else if (extension === 'json') {
        const stream = await blob2Text(file);
        this.d2.drawPart(file.name, stream);
      }
    },
    // 切换在预览图中的显示和隐藏效果
    togglePreviewerEffect({ fullName, extension, visible }) {
      if (extension === 'json') {
        this.d2.setPartVisible(fullName, visible);
      } else {
        this.d4.setModelVisual(fullName, visible);
      }
    },
    // 删除文件时，对应需要消除文件在预览图中显示状态
    removePreviewerEffect({ fullName, extension }) {
      // 在预览图中隐藏效果
      if (extension === 'json') {
        this.d2.removePart(fullName);
      } else {
        this.d4.removeComponent(fullName);
      }
    },
    // 获取上传模型文件的截图OSS路径(外部函数调用)
    async getSnapshot() {
      this.d4.hideBackSide();
      const stream = this.d4.getModelSnapshot(520, 400);
      const file = dataUrl2Blob(stream);
      const path = await uploadBlobToOSS(file, 'SHOE');
      return path;
    },
  },
};
</script>

