/* 该文件与同级的index.vue 功能重复；调整上传鞋模型页面时会删除 */

<template>
  <div
    @click.stop="$refs.inputFile.click()"
    @drop.prevent="onDrop"
    @dragover.prevent
  >
    <slot></slot>

    <input
      ref="inputFile"
      :multiple="multiple"
      :accept="accept"
      type="file"
      class="hidden"
      @change="uploadByClick"
    />
  </div>
</template>

<script>
import i18n from 'i18next';

export default {
  props: {
    multiple: {
      type: Boolean,
      default: false,
    },
    // 文件之间用 , 隔开
    accept: {
      type: String,
      default: '',
    },
    // 上传文件最大限制
    maxSize: {
      type: Number,
      default: Infinity,
    },
  },
  data() {
    return {
      dragover: false,
    };
  },
  methods: {
    onDrop(e) {
      console.log(e);
      const files = Array.from(e.dataTransfer.files);
      this.$emit('upload', this.multiple ? files : files[0]);
    },
    async uploadByClick() {
      const { files } = this.$refs.inputFile;
      await this._checkFile(files[0]);
      if (files.length) {
        this.$emit('upload', this.multiple ? Array.from(files) : files[0]);
        // 两次选取同一个文件不能触发相应事件的问题
        this.$refs.inputFile.value = '';
      }
    },
    _checkFile(file) {
      return new Promise((resolve, reject) => {
        // 过滤空文件
        if (file.size === 0) {
          this.$message.warning(i18n.t('upload.msg.exceed_size', { filename: file.name }));
          reject(new Error(false));
        }

        // 校验文件是否符合上传格式; 如果没有格式要求，则不做格式校验
        if (this.accept) {
          const extensionList = this.accept
            .toLowerCase()
            .replace(/[\s/.]/g, '')
            .split(/[,;、；|]/);
          const extension = file.name.split('.').pop().toLowerCase();
          if (!extensionList.includes(extension)) {
            this.unmatchFiles.push(file);
            reject(new Error(false));
          }
        }

        // 校验文件是否符合大小规范
        if (file.size > this.maxSize) {
          this.$message.warning(i18n.t('upload.msg.exceed_size', { filename: file.name }));
          reject(new Error(false));
        }

        return resolve(true);
      });
    },
  },
};
</script>
