<template>
  <div class="upload-model-files">
    <!-- 标题 -->
    <h1 class="section-title">
      <span>{{ $t('upload.model.upload_design_files') }}</span>
      <button-disclaimer />
    </h1>

    <div class="section-content">
      <div :class="{'center-box': !uploadedFlag }">
        <!-- 上传区域 -->
        <upload-area style="width: 670px;" />

        <!-- 上传文件列表 -->
        <file-list v-if="uploadedFlag" ref="fileList" />
        <virtual-sample-link :class="{'text-align-center': uploadedFlag }" />
      </div>

      <!-- 上传文件效果图 -->
      <previewer-area :class="{'hide-box': !uploadedFlag }" ref="previewer" />
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';
import ButtonDisclaimer from '@/views/upload/components/ButtonDisclaimer';
import VirtualSampleLink from '@/views/upload/components/VirtualSampleLink';
import UploadArea from './file/UploadArea';
import FileList from './file/FileList';
import PreviewerArea from './file/PreviewerArea';

export default {
  components: {
    ButtonDisclaimer,
    UploadArea,
    FileList,
    PreviewerArea,
    VirtualSampleLink,
  },
  inject: ['vm'],
  data() {
    return {
      repeatList: [], // 记录每次重复上传的文件
      fileNames: new Set(), // 记录已经上传文件的名称
      uploadedFlag: false,
    };
  },
  mounted() {
    // 监听上传本地文件事件
    this.vm.$on('upload-files', this.handleUploadFiles);
    // 监听删除文件事件
    this.vm.$on('delete-file', this.handleDeleteFile);
  },
  methods: {
    // 处理已上传的文件
    handleUploadFiles(files) {
      // 每次上传都要清空临时存放上传文件的记录
      this.repeatList = [];
      // 改变上传状态的标示
      if (!this.uploadedFlag) this.uploadedFlag = true;

      files.forEach(file => {
        // 检查是否已在已上传文件数组中，忽略已经上传过的文件
        if (!this.fileNames.has(file.name)) {
          // 添加到已上传的Set对象中
          this.fileNames.add(file.name);

          this.$nextTick(() => {
            // 发送给负责渲染效果的组件处理
            this.vm.$emit('render-file', file);
            // 发送给负责文件列表的组件处理
            this.vm.$emit('show-file', file);
          });
        } else {
          this.repeatList.push(file);
        }
      });

      // 重复文件提示
      this._showRepeatTip();
    },
    // 在已经上传文件的名称对象中，删除对应的文件名
    handleDeleteFile({ fullName }) {
      this.fileNames.delete(fullName);
    },
    // 根据重复的文件数量，对应提示语
    _showRepeatTip() {
      const repeatLen = this.repeatList.length;
      if (repeatLen === 0) return;
      if (repeatLen <= 3) {
        const nameStr = this.repeatList.map(item => item.name).join(', ');
        // `detected ${repeatLen} repeat file${repeatLen === 1 ? '' : 's'}: ${nameStr}`
        this.$message.warning(`${i18n.t('upload.repeat_files', { num: repeatLen })}: ${nameStr}`);
      } else {
        // this.$message.warning(`detected ${repeatLen} repeat files`);
        this.$message.warning(i18n.t('upload.repeat_files', { num: repeatLen }));
      }
    },
    // 校验上传文件是否按要求的内容上传（外部调用）
    async validateFiles() {
      // 判断是否有上传ctm文件，没有上传则报错
      const hasCTMFiles = [...this.fileNames].some(file => file.includes('.ctm'));
      if (!hasCTMFiles) throw new Error(i18n.t('upload.model.ctm_required'));

      // 判断是否ctm文件是否上传完毕
      const uploadStatus = this.$refs.fileList.detectCTMUploaded();
      if (!uploadStatus) throw new Error(i18n.t('upload.model.ctm_uploading'));

      // 获取上传文件的对象
      return this.$refs.fileList.getUploadFilesList();
    },
    // 获取上传模型文件的截图OSS路径(外部函数调用)
    getModelSnapshot() {
      return this.$refs.previewer.getSnapshot();
    },
  },
};
</script>

