<template>
  <div class="file-list-section">
    <!-- 文件列表 -->
    <ul class="file-list-area scrollable-y">
      <li v-for="(file, i) in files" :key="i" class="file-item">
        <file-item-buffer :data="file" />
      </li>
    </ul>

    <!-- 文件数量统计 -->
    <p class="file-number-statisctic">
      <span class="statisctic-number">
        CTM: {{ ctm.uploaded }}/{{ ctm.total }}
      </span>
      <span class="statisctic-number">
        JSON: {{ json.uploaded }}/{{ json.total }}
      </span>
    </p>
  </div>
</template>

<script>
import uploadBlobToOSS from '@/utils/oss/uploadBlobToOSS';
import FileItemBuffer from './FileItemBuffer';


export default {
  components: {
    FileItemBuffer,
  },
  inject: ['vm'],
  data() {
    return {
      files: [], // 渲染文件列表
      ctm: {
        total: 0,
        uploaded: 0,
      },
      json: {
        total: 0,
        uploaded: 0,
      },
    };
  },
  mounted() {
    // 监听上传文件事件
    this.vm.$on('show-file', this.showFile);
    // 监听删除文件事件
    this.vm.$on('delete-file', this.deleteFile);
  },
  methods: {
    // 获取上传文件（外部调用）
    getUploadFilesList() {
      return this.files;
    },
    // 判断ctm文件是否上传完毕(外部调用)
    detectCTMUploaded() {
      let result = true;
      this.files.forEach(file => {
        if (file.ctm) {
          if (file.ctm.percentage < 100) result = false;
        }
      });
      return result;
    },
    // 将文件状态，显示到视图上
    showFile(file) {
      // 先格式化上传文件的数据结构，方便后续函数操作
      const f = this._formatFile(file);

      this._handleDifferFile(f);
      this._uploadFile(f);
    },
    // 处理删除事件时
    deleteFile({ name, extension, percentage }) {
      this.files.forEach((file, idx) => {
        if (file.name === name) {
          // 在列表中删除对应的属性
          file[extension] = null;
          // 如果列表中的ctm 和 json 为空，则在列表中删除该对象
          if (!file.ctm && !file.json) this.files.splice(idx, 1);

          // 对统计数量做调整
          this[extension].total--;
          // 如果是ctm文件，且在上传过程中删除，不用更新已上传文件的数量
          if (!(extension === 'ctm' && percentage < 100)) this[extension].uploaded--;
        }
      });
    },
    // 根据列表显示的视图，对应数据结构做处理
    _formatFile(file) {
      const temp = file.name.split('.');
      const extension = temp.pop()?.toLowerCase();
      const name = temp.join('.');

      return {
        fileObj: file,
        fullName: file.name,
        name,
        extension,
        percentage: 0,
        size: file.size,
      };
    },
    // 对不同格式的文件，对数据格式进行调整后放入到列表对象
    _handleDifferFile(file) {
      const { name, extension } = file;
      // 检测文件数组中是否已经创建过
      const result = this.files.find(item => item.name === name);
      if (result) {
        // 在已有的对象中，根据文件扩展名，添加到对应的属性上
        result[extension] = file;
        this[extension].total++;
      } else {
        // 如果没有已创建的对象，创建一个新的对象
        const obj = { name, ctm: null, json: null };
        obj[extension] = file;
        this[extension].total++;
        this.files.push(obj);
      }
    },
    // 如果是ctm文件，上传到OSS；如果是json文件，上传文件数量加
    async _uploadFile(file) {
      const { fileObj, extension, name } = file;
      // 上传部件文件到OSS
      if (extension === 'ctm') {
        const path = await uploadBlobToOSS(fileObj, 'COMPONENT', (...args) => {
          // 将上传的进度传到渲染文件中的对应属性中
          file.percentage = +((args[3] * 100).toFixed(0));
        });
        // 先判断该文件是否还存在（可能CTM文件上传过程中被删掉)
        if (this.files.some(item => item.name === name)) {
          file.path = path;
          this.ctm.uploaded++;
        }
      } else if (extension === 'json') {
        this.json.uploaded++;
      }
    },
  },
};
</script>

