<template>
  <div class="upload-model-previewer">
    <upload-drag
      class="upload-area-box"
      accept=".jpg,.jpeg,.png"
      id="dropzone"
      :max-size="300 * 1024"
      @upload="handleUploadFile">
      <!-- 上传预览图 -->
      <div v-if="imgUrl" class="previewer-box">
        <img :src="imgUrl" class="previewer">
        <div class="previewer-veil">
          <i class="iconfont icon-file-delete" @click.stop="deleteUploadFile" />
          <i class="iconfont icon-upload-cloud-o" />
          <p class="upload-text">{{ $t('upload.reupload') }}</p>
        </div>
      </div>
      <!-- 上传按钮 -->
      <div v-else class="upload-area">
        <i class="iconfont icon-upload-cloud-o" />
        <p class="upload-text">{{ $t('upload.upload') }}</p>
      </div>
    </upload-drag>

    <p class="upload-tip">
      {{ $t('upload.support_format') }}: <span class="text-strong">.jpg .png</span>
      <br />
      <i18n path="upload.upload_preview_tip1">
        <span place="dimension" class="text-strong">260 x 200</span>
      </i18n>
      <br />
      <i18n path="upload.upload_preview_tip2">
        <span place="size" class="text-strong">300 KB</span>
      </i18n>
      <!-- <br />Recommended image dimension is <span class="text-strong">260 x 200</span> pixels,
      <br />and not to exceed <span class="text-strong">300 KB</span> in size. -->
    </p>
  </div>
</template>

<script>
import UploadDrag from '@/components/upload/UploadDrag';

import uploadBlobToOSS from '@/utils/oss/uploadBlobToOSS';

export default {
  components: {
    UploadDrag,
  },
  data() {
    return {
      imgUrl: '',
    };
  },
  methods: {
    // 上传材质预览图
    async handleUploadFile(file) {
      this.imgUrl = window.URL.createObjectURL(file);
      const path = await uploadBlobToOSS(file, 'SHOE');
      this.$emit('upload', path);
    },
    // 删除材质预览图
    deleteUploadFile() {
      this.imgUrl = '';
      this.$emit('cancel');
    },
  },
};
</script>

